.movies {
  /* display: flex;
    flex-wrap: wrap;
    gap: 40px; */
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.content {
  flex-grow: 1;
}

.movies img {
  aspect-ratio: 1 / 1;
  /* height: 100%; */
}

.input-field {
  width: 100% !important;
}

label:not(:last-child) {
  margin-right: 1rem;
}